@import url(https://fonts.googleapis.com/css?family=Ubuntu);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  background-color: rgb(174, 210, 250);
  font-family: 'Ubuntu',
  serif;
}

.App {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  margin: 0 auto;
  padding: 10px;
  max-width: 1200px;
  box-sizing: border-box;
}

.streets,
.houses {
  width: 350px;
}

h2 {
  font-size: 32px;
  text-align: center;
  text-decoration: underline;
}

.image {
  width: 320px;
  height: 250px;
  overflow: hidden;

}

img {
  width: 100%;
  height: auto;
  box-sizing: border-box;
  border: 2px solid black;
  background: white;
  padding: 10px;
}
.streets p {
  font-size: 22px;
  border: 2px solid black;
  padding: 10px;
  box-sizing: border-box;
  background: white;
}

.streets p:hover {
  border: 2px solid transparent;
  background: rgb(64, 139, 224);
  color: white;
  cursor: pointer;
}
.house {
  width: 350px;
  height: 220;
  overflow: hidden;
}

.house img:hover {
  background: rgb(64, 139, 224);
  cursor: pointer;
}
.houseDetails {
 width: 350px;
  box-sizing:border-box;

}

.houseDetails p {
  font-size: 22px;
}
